
import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { useEffect, useMemo, useCallback } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { styled } from '@mui/material/styles';
import { LoadingButton } from '@mui/lab';

import axios from 'axios';
import { Card, Grid, Stack, Box, Typography, InputAdornment } from '@mui/material';
import moment from 'moment';
import {HOST_API} from '../../../config'

// routes
import { PATH_DASHBOARD } from '../../../routes/paths';

// components
import {
  FormProvider,
  // RHFSwitch,
  RHFSelect,
  // RHFEditor,
  RHFTextField,
  RHFRadioGroup,
  // RHFUploadMultiFile,
  RHFDatePicker,
} from '../../../components/hook-form';

// ----------------------------------------------------------------------

const PAID_STATUS = ['UnPaid', 'Paid', 'Over Due', 'Cancelled'];

const PAYMENT_OPTION = [
  { group: 'Payment', classify: ['Card', 'Check', 'Cash'] }
];

const DURATION_OPTION = [
  { group: 'Duration', classify: [1, 2, 3] }
];


const LabelStyle = styled(Typography)(({ theme }) => ({
  ...theme.typography.subtitle2,
  color: theme.palette.text.secondary,
  marginBottom: theme.spacing(1),
}));



// ----------------------------------------------------------------------

ProductNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentProduct: PropTypes.object,
};

export default function ProductNewForm({ isEdit, currentProduct }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const NewProductSchema = Yup.object().shape({
    customerName: Yup.string().required('Customer Name is required'),
    invoiceId: Yup.string().required('invoice Id is required'),
    customerPhone: Yup.string().required('customer Phone is required'),
    customerAddress: Yup.string().required('Customer Address is required'),
    // amountTwo: Yup.string().required('Amount is required'),
    // amountOne: Yup.string().required('Amount is required'),
    // description: Yup.string().required('Description is required'),
    // images: Yup.array().min(1, 'Images is required'),
    // price: Yup.number().moreThan(0, 'Price should not be €0.00'),
  });

  const defaultValues = useMemo(
    () => ({
      _id: currentProduct?._id || '',
      invoiceId: currentProduct?.invoiceId || '',
      customerName: currentProduct?.customerName || '',
      customerPhone: currentProduct?.customerPhone || '',
      invoiceDate: currentProduct?.invoiceDate || Date.now(),
      customerAddress: currentProduct?.customerAddress || '',
      userId: currentProduct?.userId || '',
      vatOne: currentProduct?.vatOne || 5,
      vatTwo: currentProduct?.vatTwo || 10,
      amountOne: currentProduct?.amountOne || '',
      amountTwo: currentProduct?.amountTwo || '',
      totalOne: currentProduct?.totalOne || 0,
      totalTwo: currentProduct?.totalTwo || 0,
      grandTotal: currentProduct?.grandTotal || 0,
      paidStatus: currentProduct?.paidStatus || PAID_STATUS[0],
      due: currentProduct?.due || DURATION_OPTION[0].classify[0],
      paymentType: currentProduct?.paymentType || PAYMENT_OPTION[0].classify[0],
      // category: currentProduct?.category || CATEGORY_OPTION[0].classify[1],
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentProduct]
  );

  const methods = useForm({
    resolver: yupResolver(NewProductSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    setValue,
    getValues,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  const values = watch();

  useEffect(() => {
    if (isEdit && currentProduct) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentProduct]);

  const getTotalCosts = () => {
    return values.totalOne + values.totalTwo;
  };

  const onSubmit = async () => {
    if (values._id === "") { delete values._id }
    if(values.grandTotal===0){
      enqueueSnackbar('Please Enter the amount' ,{ variant: "error" });

    }else{
      try {
        await new Promise((resolve) => setTimeout(resolve, 500));
        await axios.post(`${HOST_API}invoice/save`, values)
          .then((response) => {
            console.log(response);
            if(response.data.status === 1){
              reset();
              enqueueSnackbar(!isEdit ? 'Create success!' : 'Update success!');
              navigate(PATH_DASHBOARD.eCommerce.list);
            }else{
              enqueueSnackbar('Invoice Id already exist' ,{ variant: "error" });
            }
          
          }, (error) => {
            console.log(error);
          });
  
      } catch (error) {
        console.error(error);
      }
    }
    
  };
  const getCustomer = async (phone) => {

    // console.log(getCustomerId(phone));
    const response = axios.post(`${HOST_API}customer/customerDetails`, { 'phone': phone })
      .then((response) => {
        console.log('----', response.data);
        if (response.data.status === 1) {
          setValue('customerName', response.data.response.name)
          setValue('customerAddress', response.data.response.address)
          setValue('userId', response.data.response._id)
        }else{
          setValue('userId', '')
          setValue('customerName', '')
          setValue('customerAddress', '')
        }
        // e.preventDefault()

      })
  };
  /* const handleDrop = useCallback(
    (acceptedFiles) => {
      setValue(
        'images',
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
    [setValue]
  ); */

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>

          <Card sx={{ p: 4 }}>
            <LabelStyle>Invoice Details</LabelStyle>
            <Stack spacing={3}>
              <Box
                sx={{
                  display: 'grid',
                  columnGap: 2,
                  rowGap: 3,
                  gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
                }}
              >
                <RHFTextField name="invoiceId" label="Invoice" />

                <RHFDatePicker name="invoiceDate" label="InvoiceDate"
                  value={getValues('invoiceDate')}
                  onChange={(newValue) => {
                    console.log("onChange", newValue);
                    setValue(moment(Date.now()).format("DD/MM/YYYY"));
                  }} />



                <RHFTextField name="customerPhone" label="Customer Phone" onBlur={(event) => {
                  getCustomer(getValues('customerPhone'))
                }
                } />
                <RHFTextField name="customerName" label="Customer Name" />
                <RHFTextField name="customerAddress" label="Customer Address" />
                {/* <RHFTextField name="duration" label="Duration" />  */}


              </Box>

              <LabelStyle>Amount Details</LabelStyle>

              <Box sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(3, 1fr)' },
              }} >


                <RHFTextField
                  name="amountOne"
                  label="Amount"
                  placeholder="0.00"
                  value={getValues('amountOne') === 0 ? '' : getValues('amountOne')}
                  onChange={(event) => {
                    setValue('amountOne', Number(event.target.value))
                    setValue('totalOne', Number(Number(event.target.value)) + Number(Number(event.target.value) * (getValues('vatOne') / 100)))
                    setValue('grandTotal', Number(getValues('totalOne')).toFixed(2) + Number(getValues('totalTwo')).toFixed(2))
                  }
                  }
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">€</InputAdornment>,
                    type: 'number',
                  }}
                />
                <RHFTextField name="vatOne" value={`${getValues('vatOne')}%`} label="Vat" disabled />

                <RHFTextField
                  name="totalOne"
                  label="Total"
                  placeholder="0.00"
                  disabled
                  value={getValues('totalOne') === 0 ? '' : getValues('totalOne')}
                  onChange={(event) => setValue('totalOne', Number(event.target.value))}
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">€</InputAdornment>,
                    type: 'number',
                  }}
                />

              </Box>
              <Box sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(3, 1fr)' },
              }} >

                <RHFTextField
                  name="amountTwo"
                  label="Amount"
                  placeholder="0.00"
                  value={getValues('amountTwo') === 0 ? '' : getValues('amountTwo')}
                  onChange={(event) => {
                    setValue('amountTwo', Number(event.target.value))
                    setValue('totalTwo', Number(Number(event.target.value)) + Number(Number(event.target.value) * (getValues('vatTwo') / 100)))
                    setValue('grandTotal', getValues('totalOne') + getValues('totalTwo'))
                  }}
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">€</InputAdornment>,
                    type: 'number',
                  }}
                />
                <RHFTextField name="vatTwo" value={`${getValues('vatTwo')}%`} label="Vat" disabled />
                <RHFTextField
                  name="totalTwo"
                  label="Total"
                  placeholder="0.00"
                  disabled
                  value={getValues('totalTwo') === 0 ? '' : getValues('totalTwo')}
                  onChange={(event) => setValue('totalTwo', Number(event.target.value))}
                  InputLabelProps={{ shrink: true }}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">€</InputAdornment>,
                    type: 'number',
                  }}
                />

              </Box>
            </Stack>
          </Card>
        </Grid>

        <Grid item xs={12} md={4}>
          <Stack spacing={3}>
            <Card sx={{ p: 3 }}>
              {/* <RHFSwitch name="inStock" label="In stock" /> */}
              <Stack spacing={3} mt={2}>
                <RHFSelect name="paymentType" label="paymentType">
                  {PAYMENT_OPTION.map((category) => (
                    <optgroup key={category.group} label={category.group}>
                      {category.classify.map((classify) => (
                        <option key={classify} value={classify}>
                          {classify}
                        </option>
                      ))}
                    </optgroup>
                  ))}
                </RHFSelect>
                <RHFSelect name="due" label="Duration">
                  {DURATION_OPTION.map((category) => (
                    <optgroup key={category.group} label={category.group}>
                      {category.classify.map((classify) => (
                        <option key={classify} value={classify}>
                          {classify} Month
                        </option>
                      ))}
                    </optgroup>
                  ))}
                </RHFSelect>

                <div>
                  <LabelStyle>Status</LabelStyle>
                  <RHFRadioGroup
                    name="paidStatus"
                    options={PAID_STATUS}
                    sx={{
                      '& .MuiFormControlLabel-root': { mr: 4 },
                    }}
                  />
                </div>
                <Typography variant="subtitle2" sx={{ color: 'text.secondary' }}>
                  Grand Total
                </Typography>
                <Typography variant="body2" sx={{ color: 'text.secondary', fontSize: 24 }}>
                  € {getTotalCosts().toFixed(2)}
                </Typography>
                {/* <LabelStyle>Grand Total</LabelStyle>
                <h1 className="fw-bold">€ {getTotalCosts().toFixed(2)}</h1> */}
                <LoadingButton type="submit" variant="contained" size="large" loading={isSubmitting}>
                  {!isEdit ? 'Create invoice' : 'Save Changes'}
                </LoadingButton>
              </Stack>
            </Card>




          </Stack>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
